@import './var.scss';
.sunCurve-wrap {
  padding: 0 10px;
  .sunCurve-header-tvl {
    padding: 60px 0 40px;
    text-align: center;
    font-family: Avenir Next;
    .sunCurve-header-title {
      margin-bottom: 5px;
      font-size: 15px;
      font-weight: 400;
      color: rgba(255, 255, 255, 1);
      line-height: 28px;
    }
    .sunCurve-header-value {
      font-size: 40px;
      font-weight: bold;
      color: #ffffff;
      line-height: 56px;
    }
  }
  .sunCurve-main {
    margin: 0 auto;
    max-width: 1200px;
    .list-holder {
      min-height: 200px;
    }
  }
  .sunCurve-main-inner {
    border-radius: 20px;
    background: #2a2a3f;
    overflow: hidden;
  }
  .sunCurve-thead {
    display: flex;
    padding: 0 40px;
    font-size: 15px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    background: rgba(255, 255, 255, 0.05);
    color: rgba(255, 255, 255, 0.7);
    line-height: 21px;
    .sunCurve-thead-l,
    .sunCurve-thead-c {
      margin-right: 10px;
    }
    .sunCurve-thead-l,
    .sunCurve-thead-c,
    .sunCurve-thead-r {
      padding: 20px 0;
    }
    .sunCurve-thead-l {
      width: calc(max(50%, 500px));
    }
    .sunCurve-thead-c,
    .sunCurve-thead-r {
      flex: 1;
    }
  }
  .sunCurve-list {
    li {
      position: relative;
      display: flex;
      padding: 20px 40px;
      font-size: 15px;
      background: #2a2a3f;
      line-height: 30px !important;
      border-bottom: 1px solid rgba(#fff, 0.1);
      cursor: pointer;
      &:last-child {
        border-bottom: 0;
      }
      .sunCurve-value {
        font-size: 20px;
        font-weight: 600;
        font-family: Avenir Next;
        color: #ffffff;
      }
      .sunCurve-label {
        font-size: 15px;
        font-weight: 100;
        font-family: PingFangSC-Regular, PingFang SC;
        color: rgba(255, 255, 255, 0.7);
      }
    }
    .sunCurve-row-l,
    .sunCurve-row-c {
      margin-right: 10px;
    }
    .sunCurve-row-c,
    .sunCurve-row-r {
      .sunCurve-label {
        display: none;
      }
    }
    .sunCurve-row-l {
      width: calc(max(50%, 500px));
      .sunCurve-value {
        margin-right: 10px;
      }
    }
    .sunCurve-row-c {
      flex: 1;
    }
    .sunCurve-row-r {
      flex: 1;
    }
    .sunCurve-row {
      display: flex;
    }
    .sunCurve-row-l {
      .pool-icon {
        margin-right: 10px;
        height: 30px;
        img {
          display: block;
          height: 100%;
        }
      }
    }
    .icon-right {
      position: absolute;
      right: 30px;
      top: 50%;
      margin-top: -5px;
      width: 10px;
      height: 10px;
    }
  }
  .more-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 25px 0;
    line-height: 21px;
    font-size: 15px;
    background: #2a2a3f;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    border-top: 1px solid rgba(#fff, 0.1);
    img {
      display: block;
      margin-left: 10px;
      width: 10px;
      height: 10px;
    }
  }
  .more-btn-active {
    img {
      transform: rotate(180deg);
    }
  }
  .sunCurve-loading {
    .ant-spin-container::after {
      background: transparent;
    }
  }
}

@include mobile-only {
  .sunCurve-wrap {
    padding: 0 15px 40px;
    .sunCurve-main {
      max-width: 100%;
    }
    .sunCurve-header-tvl {
      padding: 16px 0 20px;
      .sunCurve-header-title {
        font-size: 12px;
        line-height: 17px;
      }
      .sunCurve-header-value {
        font-size: 24px;
        line-height: 33px;
      }
    }
    .sunCurve-thead {
      display: none;
    }
    .sunCurve-main-inner {
      border-radius: 0px;
      background: transparent;
      overflow: visible;
    }
    .sunCurve-list {
      li {
        flex-wrap: wrap;
        padding: 10px;
        font-size: 12px;
        color: rgba(255, 255, 255, 1);
        margin-bottom: 12px;
        line-height: 17px !important;
        background: #2a2a3f;
        box-shadow: 0px 0px 4px 0px rgba(21, 21, 38, 0.5);
        border-radius: 5px;
        border: 0;
        .sunCurve-value {
          font-size: 12px;
          font-weight: 600;
          color: #ffffff;
        }
        .sunCurve-label {
          font-size: 12px;
          font-weight: 400;
          color: rgba(255, 255, 255, 0.5);
        }
      }
      .sunCurve-row-c,
      .sunCurve-row-r {
        .sunCurve-label {
          margin-right: 6px;
        }
      }
      .sunCurve-row-l {
        width: 100%;
        line-height: 20px;
        margin-right: 0;
        margin-bottom: 10px;
        .sunCurve-value {
          font-size: 12px;
          margin-right: 10px;
        }
      }
      .sunCurve-row-c,
      .sunCurve-row-r {
        .sunCurve-label {
          display: block;
        }
      }
      .sunCurve-row-c {
        flex: 1;
        margin-left: 0;
      }
      .sunCurve-row-r {
        flex: 1;
      }
      .sunCurve-row-l {
        .pool-icon {
          margin-right: 6px;
          height: 20px;
        }
      }
    }
    .more-btn {
      padding: 10px 0;
      line-height: 17px;
      font-size: 12px;
      border-radius: 5px;
      border: 0;
      background: #2a2a3f;
      box-shadow: 0px 0px 4px 0px rgba(21, 21, 38, 0.5);
      img {
        margin-left: 7px;
      }
    }
  }
}
